<template>
    <div :id="config.id"
         :style="calcStyle"
         class="plugin-ins">
        <slot></slot>
    </div>
</template>

<script>
    import DataProxy from '../../../proxy/DataProxy'
    import CONST from '../../../../config/const'
    import _ from 'lodash'

    export default {
        name: 'Plugin',
        props: ['config'],
        data() {
            return {
                // 数据代理实例
                dataProxy: null,
                // 数据代理线程
                proxyThread: null
            }
        },
        created() {
            this.initStyle()
            this.initProxy()
            this.initTransform()
        },
        mounted() {
            this.init()

        },
        beforeDestroy() {
            this.$store.commit(CONST.MUTATIONS.UNREGISTER, {
                comp: this
            })
        },
        computed: {
            /**
             * 计算容器样式
             */
            calcStyle() {
                return {
                    width: this.config.style.width + 'px',
                    height: this.config.style.height + 'px',
                    opacity: this.config.style.opacity,
                    display: this.config.style.display,
                }
            }
        },
        methods: {
            /**
             * 初始化组件
             */
            init() {
                this.instanceProxy()
            },
            /**
             * 初始化样式，对新组件使用默认样式
             */
            initStyle() {
                let style = _.assignIn({}, {
                    top: 30,
                    left: 30,
                    opacity: 1,
                    display: 'block',
                }, this.config.style)
                this.config.style = style
            },
            /**
             * 初始化数据代理的配置参数
             */
            initProxy() {
                this.config.proxy = _.assignIn({}, {
                    // 类型，0-API / 1-静态数据 / 2-数据库 / null - 无数据源
                    type: 1,
                    // 自动更新
                    autoRequest: false,
                    // 自动更新间隔
                    interval: 60,
                    // url
                    url: null,
                    // 参数
                    param: [{key: null, value: null}],
                }, this.config.proxy)
            },
            /**
             * 实例化proxy，并缓存为组件属性
             */
            instanceProxy() {
                this.dataProxy = new DataProxy(this)
            },
            /**
             * 初始化transform
             */
            initTransform() {
                let transform = _.assignIn({}, {
                    // 从源数据的什么属性获取展示用的数据集
                    prop: null,
                }, this.config.transform)
                this.config.transform = transform
            },
            /**
             * 使用数据代理获取数据，并将数据载入配置项中，然后重新加载配置项，渲染组件
             */
            render() {
                clearInterval(this.proxyThread)
                this.proxyData()
                if (this.config.proxy.autoRequest && this.config.proxy.interval) {
                    this.proxyThread = setInterval(this.proxyData, this.config.proxy.interval * 1000)
                }
            },
            /**
             * 代理数据
             */
            proxyData() {
                this.dataProxy.do()
                    .then((data) => {
                        this.config.data = data
                    })
                    .catch(() => {
                    })
                    .finally(() => {
                        this.loadData()
                    })
            },
            /**
             * 调用父组件的handler方法，handler定义了将数据载入option的过程，
             * 因为option配置项是根据具体的报表类型而不同的，所以handler必须定义在父组件中
             */
            loadData() {
                if (!this.$parent || !this.$parent.handler) {
                    return
                }
                this.$parent.handler()
            },
            /**
             * 向全局状态管理发起状态修改请求
             */
            commit(stateProp, value) {
                this.$store.commit(CONST.MUTATIONS.MODIFY, {
                    store: stateProp,
                    value: value
                })
            },
            /**
             * 重新加载报表
             */
            reload() {
                this.render()
            },
            /**
             * 触发事件代理
             */
            onEvent(eventName, item, index) {
                if (!this.config.event || !this.config.event.length) {
                    return
                }
                // 遍历组件的事件列表
                for (let i = 0; i < this.config.event.length; i++) {
                    let event = this.config.event[i]
                    // 事件名与当前触发的事件不匹配时，跳过
                    if (event.name != eventName) {
                        continue
                    }
                    // 触发的事件未定义绑定关系时，跳过
                    if (!event.binders || !event.binders.length) {
                        continue
                    }
                    // 遍历绑定规则
                    for (let j = 0; j < event.binders.length; j++) {
                        let binder = event.binders[j]
                        // 未与全局状态绑定的规则，跳过
                        if (!binder.state) {
                            continue
                        }
                        // 通过prop是否为空，判断是内置规则还是自定义规则
                        let value
                        // 如果是内置规则，则在option.data对应的对象中查找值
                        // 如果是自定义规则，则通过索引找到原始数据对象，在其中查找值
                        if (binder.prop) {
                            value = item[binder.prop]
                        } else {
                            let dataset = this.config.data
                            value = dataset[index][binder.name]
                        }
                        this.commit('screen.design.interaction.' + binder.state, value)
                    }
                }
            },
        },
        watch: {
            /**
             * 监听proxy，发生变更时，重新实例化数据代理，并渲染组件
             */
            'config.proxy': {
                deep: true,
                handler: function () {
                    this.instanceProxy()
                    this.render()
                }
            },
            /**
             * 监听全局状态绑定，发生变更时重新渲染组件
             */
            'config.store': {
                deep: true,
                handler: function () {
                    this.render()
                }
            },
            /**
             * 监听静态数据
             */
            'config.data': {
                deep: true,
                handler: function () {
                    this.loadData()
                }
            },
            /**
             * 监听数据转换规则
             */
            'config.transform': {
                deep: true,
                handler: function () {
                    this.loadData()
                }
            },
            /**
             * 监听从数据转为option的方法
             */
            'config.handler': {
                deep: true,
                handler: function () {
                    this.loadData()
                }
            },
        }
    }
</script>
