<template>
    <div class="layer">
        <div class="layer-shift">
            <template v-for="(item, index) in shiftButton">
                <el-button type="text" :key="index" @click="moveCard($event, index)">
                    <i :class="'icon-' + shiftButton[index]" class="icon iconfont"></i>
                </el-button>
            </template>
        </div>
        <div class="layer-list">
            <template v-for="(id, index) in config.sequence">
                <div :class="state.index === index ? 'active' : ''"
                     :key="index"
                     class="item"
                     @click="activeCard($event, index)">
                    <img :src="'./img/components/' + config.components[id].category + '/' + config.components[id].name + '.png'">
                    <span>{{config.components[id].text}}</span>
                </div>
            </template>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Layer",
        props: ["config", "state"],
        data() {
            return {
                shiftButton: ["top", "up", "down", "bottom"]
            }
        },
        methods: {
            /**
             * 选中一个组件卡片
             * @param index
             */
            activeCard(e, index) {
                // 阻止冒泡，防止全屏点击取消选中组件
                e.stopPropagation()
                if (this.state.index === index) {
                    return
                }
                this.state.index = index
            },
            /**
             * 移动选中的组件卡片
             * @param index
             */
            moveCard(e, index) {
                e.stopPropagation()
                if (this.state.index === undefined || this.state.index === null) {
                    return
                }
                let item
                switch (index) {
                    case 0:
                        if (this.state.index === 0) {
                            return
                        }
                        item = this.config.sequence[this.state.index]
                        this.config.sequence.splice(this.state.index, 1)
                        this.config.sequence.unshift(item)
                        this.state.index = 0
                        break
                    case 1:
                        if (this.state.index === 0) {
                            return
                        }
                        item = this.config.sequence.slice(this.state.index - 1, this.state.index + 1).reverse()
                        this.config.sequence.splice(this.state.index - 1, 2, item[0], item[1])
                        this.state.index -= 1
                        break
                    case 2:
                        if (this.state.index === this.config.sequence.length) {
                            return
                        }
                        item = this.config.sequence.slice(this.state.index, this.state.index + 2).reverse()
                        this.config.sequence.splice(this.state.index, 2, item[0], item[1])
                        this.state.index += 1
                        break
                    case 3:
                        if (this.state.index === this.config.sequence.length) {
                            return
                        }
                        item = this.config.sequence[this.state.index]
                        this.config.sequence.splice(this.state.index, 1)
                        this.config.sequence.push(item)
                        this.state.index = this.config.sequence.length - 1
                        break
                    default:
                        break
                }
            }
        }
    }
</script>

