import DataSourceType from './DataSourceType'
import _ from "lodash"

class DataProxy {

    /**
     * 根据组件的config.proxy属性，生成数据代理
     */
    constructor(component) {
        if (!component ||
            !component.config ||
            !component.config.proxy ||
            component.config.proxy.type === undefined ||
            component.config.proxy.type === null) {
            return
        }
        this.component = component
        let script = DataSourceType.getScript(this.component.config.proxy.type)
        let Clazz = require('./entity/' + script + '.js')
        this.dataSource = new Clazz.default(this.component)
    }

    /**
     * 执行数据代理
     * @returns {Promise<any>}
     */
    do() {
        return new Promise((resolve, reject) => {
            if (this.component.config.proxy.type === 1) {
                return
            }
            this.dataSource.do()
                .then((response) => {
                    let data = response
                    if (response.data) {
                        data = response.data
                    }
                    if (this.component.config.transform.prop) {
                        data = _.get(data, this.component.config.transform.prop)
                    }
                    resolve(data)
                })
                .catch((error) => {
                    reject(error)
                })
        })
    }
}

export default DataProxy
