<template>
    <div v-if="state.contextMenu.index !== undefined && state.contextMenu.index !== null"
         :style="calcStyle"
         class="context-menu">
        <div class="item" @click="moveCard($event, 'top')">
            <i class="icon iconfont icon-top"></i> 置顶
        </div>
        <div class="item" @click="moveCard($event, 'up')">
            <i class="icon iconfont icon-up"></i> 上移一层
        </div>
        <div class="item" @click="moveCard($event, 'down')">
            <i class="icon iconfont icon-down"></i> 下移一层
        </div>
        <div class="item" @click="moveCard($event, 'bottom')">
            <i class="icon iconfont icon-bottom"></i> 置底
        </div>
        <div class="separate-line"></div>
        <div v-show="showExportPic()"
             class="item"
             @click="exportPic">
            <i class="icon iconfont icon-copy"></i> 导出图片
        </div>
        <div class="item" @click="duplicateCmp">
            <i class="icon iconfont icon-copy"></i> 复制
        </div>
        <div class="item" @click="deleteCmp">
            <i class="icon iconfont icon-delete"></i> 删除
        </div>
    </div>
</template>

<script>
    import echarts from 'echarts'
    import _ from 'lodash'

    export default {
        name: 'ContextMenu',
        props: ['config', 'state'],
        data() {
            return {}
        },
        computed: {
            calcStyle() {
                return {
                    top: this.state.contextMenu.style.top + 'px',
                    left: this.state.contextMenu.style.left + 'px',
                }
            },
        },
        methods: {
            /**
             * 是否显示导出图片按钮
             */
            showExportPic() {
                let index = this.state.contextMenu.index
                let id = this.config.sequence[index]
                return this.config.components[id].category &&
                    (this.config.components[id].category == 'map' || this.config.components[id].category == 'chart')
            },
            /**
             * 导出图片
             */
            exportPic() {
                let index = this.state.contextMenu.index
                let id = this.config.sequence[index]
                let chart = echarts.getInstanceByDom(document.getElementById(id))
                let pic = chart.getDataURL()
                let aTag = document.createElement('a')
                aTag.download = "pic-" + new Date().getTime() + ".png"
                aTag.href = pic
                aTag.click()
            },
            /**
             * 复制一个组件的副本
             */
            duplicateCmp() {
                let index = this.state.contextMenu.index
                let id = this.config.sequence[index]
                let component = _.cloneDeep(this.config.components[id])
                component.config.id = 'cmp-' + new Date().getTime()
                component.config.style.top += 50
                component.config.style.left += 50
                this.config.sequence.push(component.config.id)
                this.$set(this.config.components, component.config.id, component)
            },
            /**
             * 删除组件
             */
            deleteCmp() {
                let index = this.state.contextMenu.index
                let id = this.config.sequence[index]
                this.config.sequence.splice(index, 1)
                delete this.config.components[id]
            },
            /**
             * 移动选中的组件
             * @param index
             */
            moveCard(e, action) {
                let item
                switch (action) {
                    case 'top':
                        if (this.state.index === 0) {
                            return
                        }
                        item = this.config.sequence[this.state.index]
                        this.config.sequence.splice(this.state.index, 1)
                        this.config.sequence.unshift(item)
                        this.state.index = 0
                        break
                    case 'up':
                        if (this.state.index === 0) {
                            return
                        }
                        item = this.config.sequence.slice(this.state.index - 1, this.state.index + 1).reverse()
                        this.config.sequence.splice(this.state.index - 1, 2, item[0], item[1])
                        this.state.index -= 1
                        break
                    case 'down':
                        if (this.state.index === this.config.sequence.length) {
                            return
                        }
                        item = this.config.sequence.slice(this.state.index, this.state.index + 2).reverse()
                        this.config.sequence.splice(this.state.index, 2, item[0], item[1])
                        this.state.index += 1
                        break
                    case 'bottom':
                        if (this.state.index === this.config.sequence.length) {
                            return
                        }
                        item = this.config.sequence[this.state.index]
                        this.config.sequence.splice(this.state.index, 1)
                        this.config.sequence.push(item)
                        this.state.index = this.config.sequence.length - 1
                        break
                    default:
                        break
                }
            }
        }
    }
</script>

