<template>
    <div class="config-data">
        <template v-if="component.config.transform.mapper || component.config.proxy.type">
            <div class="separate-line"
                 v-if="component.config.transform.mapper"></div>
            <div class="config-mapper"
                 v-if="component.config.transform.mapper">
                <el-row>
                    <el-col :span="6" class="tag">字段</el-col>
                    <el-col :span="12" class="tag">映射</el-col>
                    <el-col :span="6" class="tag" align="center">状态</el-col>
                </el-row>
                <template v-for="(value, key) in component.config.transform.mapper">
                    <el-row>
                        <el-col :span="6" class="tag">{{key}}</el-col>
                        <el-col :span="12">
                            <el-input v-model="component.config.transform.mapper[key]"
                                      placeholder="输入属性名"
                                      size="mini" clearable
                                      @blur="validateMapper(key, value)">
                            </el-input>
                        </el-col>
                        <el-col :span="6" align="center">
                            <el-tag v-if="validate[key] != undefined && validate[key] != null"
                                    size="mini"
                                    :type="validate[key] ? 'success' : 'danger'">
                                {{validate[key] ? '通过' : '失败'}}
                            </el-tag>
                        </el-col>
                    </el-row>
                </template>
            </div>
            <div class="separate-line"></div>
            <div class="config-source">
                <el-row>
                    <el-col :span="6" class="tag">数据源类型</el-col>
                    <el-col :span="14">
                        <template v-if="dataSource && dataSource.list">
                            <el-select v-model="component.config.proxy.type"
                                       placeholder="请选择"
                                       size="mini"
                                       @change="changeDataSourceType">
                                <el-option v-for="item in dataSource.list"
                                           :key="item.value"
                                           :label="item.name"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                        </template>
                    </el-col>
                    <el-col :span="4">
                        <el-button v-show="showBtnAddDataSource"
                                   type="text"
                                   size="mini"
                                   @click="showAddDataSource">新增
                        </el-button>
                    </el-col>
                </el-row>

                <!-- API -->
                <template v-if="component.config.proxy.type==0">
                    <el-row>
                        <el-col :span="6" class="tag">URL</el-col>
                        <el-col :span="18">
                            <el-input v-model="component.config.proxy.url"
                                      size="mini"
                                      placeholder="请输入url"
                                      @blur="changeUrl"></el-input>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="6" class="tag">请求方式</el-col>
                        <el-col :span="9">
                            <el-select v-model="component.config.proxy.method"
                                       placeholder="请选择"
                                       size="mini">
                                <el-option v-for="item in apiMethod"
                                           :key="item.value"
                                           :label="item.name"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                        </el-col>
                        <el-col :span="8" :offset="1">
                            <el-checkbox v-model="component.config.proxy.remote">服务器代理</el-checkbox>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="10" class="tag" align="center">参数</el-col>
                        <el-col :span="10" :offset="1" class="tag" align="center">值 / 绑定状态</el-col>
                        <el-col :span="2" :offset="1">
                            <el-button type="text"
                                       class="icon iconfont icon-plus"
                                       size="mini"
                                       @click="addParam"></el-button>
                        </el-col>
                    </el-row>
                    <template v-for="(arr, i) in component.config.proxy.param">
                        <el-row>
                            <el-col :span="10">
                                <el-input v-model="component.config.proxy.param[i].key"
                                          size="mini"
                                          @blur="changeParam($event, i, 'key')"></el-input>
                            </el-col>
                            <el-col :span="10" :offset="1">
                                <el-autocomplete v-model="component.config.proxy.param[i].value"
                                                 :fetch-suggestions="queryStateStore"
                                                 size="mini"
                                                 :trigger-on-focus="false"
                                                 @select="handleSelect($event,i)"
                                                 @blur="changeParam($event, i, 'value')"></el-autocomplete>
                            </el-col>
                            <el-col :span="2" :offset="1">
                                <el-button type="text"
                                           class="icon iconfont icon-minus"
                                           size="mini"
                                           @click="deleteParam(i)"></el-button>
                            </el-col>
                        </el-row>
                    </template>
                    <el-row></el-row>
                    <div class="separate-line"></div>
                    <el-row>
                        <el-col :span="6" class="tag">属性路径</el-col>
                        <el-col :span="18">
                            <el-input v-model="component.config.transform.prop"
                                      size="mini"
                                      placeholder="从返回值的a.b.c属性获取数据"></el-input>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="2">
                            <el-checkbox v-model="component.config.proxy.autoRequest"></el-checkbox>
                        </el-col>
                        <el-col :span="8" class="tag">自动更新：间隔</el-col>
                        <el-col :span="6">
                            <el-input v-model="component.config.proxy.interval" size="mini"></el-input>
                        </el-col>
                        <el-col :span="6" :offset="1" class="tag">秒请求一次</el-col>
                    </el-row>
                </template>

                <template v-else-if="component.config.proxy.type==2">
                    <el-row>
                        <el-col :span="6" class="tag">数据库</el-col>
                        <el-col :span="18">
                            <el-select v-model="component.config.proxy.dsId"
                                       placeholder="请选择"
                                       size="mini"
                                       @change="changeDbSource">
                                <el-option v-for="item in dbSource"
                                           :key="item.id"
                                           :label="item.name"
                                           :value="item.id">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="6" class="tag">SQL</el-col>
                        <el-col :span="24">
                            <el-input v-model="component.config.proxy.sql"
                                      type="textarea"
                                      :rows="6"
                                      size="mini"
                                      resize="none">
                            </el-input>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="2">
                            <el-checkbox v-model="component.config.proxy.autoRequest"></el-checkbox>
                        </el-col>
                        <el-col :span="8" class="tag">自动更新：间隔</el-col>
                        <el-col :span="6">
                            <el-input v-model="component.config.proxy.interval" size="mini"></el-input>
                        </el-col>
                        <el-col :span="6" :offset="1" class="tag">秒请求一次</el-col>
                    </el-row>
                </template>

                <template v-else-if="component.config.proxy.type==3">
                    <el-row>
                        <el-col :span="6" class="tag">文件名</el-col>
                        <el-col :span="18">
                            <el-select v-model="component.config.proxy.dsId"
                                       placeholder="请选择"
                                       size="mini"
                                       @change="changeFileSource">
                                <el-option v-for="item in fileSource"
                                           :key="item.id"
                                           :label="item.name"
                                           :value="item.id">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="2">
                            <el-checkbox v-model="component.config.proxy.autoRequest"></el-checkbox>
                        </el-col>
                        <el-col :span="8" class="tag">自动更新：间隔</el-col>
                        <el-col :span="6">
                            <el-input v-model="component.config.proxy.interval" size="mini"></el-input>
                        </el-col>
                        <el-col :span="6" :offset="1" class="tag">秒请求一次</el-col>
                    </el-row>
                </template>

                <!-- 数据展示 -->
                <el-row>
                    <el-input type="textarea"
                              :rows="16"
                              size="mini"
                              resize="none"
                              :readonly="component.config.proxy.type!==1"
                              v-model="formatData"
                              @change="changeProxyData">
                    </el-input>
                </el-row>
            </div>
        </template>
        <div v-else class="no-binder">
            无参数
        </div>
    </div>
</template>

<script>
    import API from '../../../config/api'
    import CONST from '../../../config/const'
    import DataSourceType from '../../proxy/DataSourceType'
    import _ from 'lodash'

    export default {
        name: 'Data',
        props: ['component', 'state'],
        data() {
            return {
                // 数据源类型
                dataSource: null,
                // 显示新增数据源按钮
                showBtnAddDataSource: false,
                // 缓存属性映射校验结果
                validate: {},
                // API请求发送方式
                apiMethod: [
                    {name: 'GET', value: 'GET'},
                    {name: 'POST', value: 'POST'}
                ],
                // 数据库数据源
                dbSource: [],
                // excel文件数据源
                fileSource: []
            }
        },
        created() {
            this.dataSource = DataSourceType
            if (!this.component.config.transform || !this.component.config.transform.mapper) {
                return
            }
            this.validate = _.assign({}, this.component.config.transform.mapper)
            for (let key in this.component.config.transform.mapper) {
                this.validateMapper(key)
            }
        },
        mounted() {
            this.initDbSource()
            this.initFileSource()
        },
        computed: {
            formatData() {
                return JSON.stringify(this.component.config.data, null, 4)
            }
        },
        methods: {
            /**
             * 初始化数据库数据源
             */
            initDbSource() {
                this.ajax({
                    url: API.DATA_SOURCE.LIST_DB_BY_USER,
                    method: CONST.AJAX.POST,
                }).then(response => {
                    this.dbSource = response.data
                })
            },
            /**
             * 初始化Excel文件数据源
             */
            initFileSource() {
                this.ajax({
                    url: API.DATA_SOURCE.LIST_FILE_BY_USER,
                    method: CONST.AJAX.POST,
                }).then(response => {
                    this.fileSource = response.data
                })

            },
            /**
             * 修改url
             */
            changeUrl(e) {
                this.$set(this.component.config.proxy, 'url', e.target.value)
            },
            /**
             * 修改参数
             */
            changeParam(e, i, prop) {
                this.$set(this.component.config.proxy.param[i], prop, e.target.value)
            },
            /**
             * 添加参数对
             */
            addParam() {
                if (!this.component.config.proxy.param || !this.component.config.proxy.param.length) {
                    this.$set(this.component.config.proxy, 'param', [])
                }
                this.component.config.proxy.param.push({key: null, value: null})
            },
            /**
             * 删除参数对
             */
            deleteParam(index) {
                this.component.config.proxy.param.splice(index, 1)
            },
            /**
             * 将缓存的静态数据存入proxy.data
             */
            changeProxyData(value) {
                this.component.config.data = JSON.parse(value)
            },
            /**
             * 验证属性映射关系
             * @param event
             * @param value
             * @param key
             */
            validateMapper(key) {
                let data = this.component.config.data
                if (!data || !data.length) {
                    this.validate[key] = false
                    return
                }
                if (this.component.config.data instanceof Array) {
                    data = data[0]
                }
                let value = this.component.config.transform.mapper[key]
                if (!value) {
                    if (data.hasOwnProperty(key)) {
                        this.validate[key] = true
                        return
                    } else {
                        this.validate[key] = false
                        return
                    }
                } else {
                    if (data.hasOwnProperty(value)) {
                        this.validate[key] = true
                        return
                    } else {
                        this.validate[key] = false
                        return
                    }
                }
            },
            /**
             * 查询全局变量
             * @param queryString
             * @param cb
             */
            queryStateStore(queryString, cb) {
                if (!queryString.startsWith(':')) {
                    return
                }
                let results = []
                let kw = queryString.substring(1)
                for (let key in this.$store.state.screen.design.interaction) {
                    if (key.startsWith(kw)) {
                        results.push({value: ':' + key})
                    }
                }
                cb(results)
            },
            /**
             * 选中自动完成
             */
            handleSelect(item, i) {
                this.component.config.proxy.param[i].value = item.value
            },
            /**
             * 修改数据源类型
             */
            changeDataSourceType(val) {
                this.showBtnAddDataSource = this.dataSource.get(val).optAdd
            },
            /**
             * 选择数据库数据源
             */
            changeDbSource(val) {
                for (let i = 0; i < this.dbSource.length; i++) {
                    let db = this.dbSource[i]
                    if (db.id === val) {
                        this.component.config.proxy.dsType = db.type
                        break
                    }
                }
            },
            /**
             * 显示增加数据源窗口
             */
            showAddDataSource() {
                this.state.addDataSourceModal = {
                    visible: true,
                    dataSource: null
                }
            },
            /**
             * 选择文件数据源
             */
            changeFileSource(val) {
                for (let i = 0; i < this.fileSource.length; i++) {
                    let file = this.fileSource[i]
                    if (file.id === val) {
                        this.component.config.proxy.dsType = file.type
                        break
                    }
                }
            }
        }
    }
</script>

<style scoped>

</style>
