<template>
    <div class="config-base">
        <el-tabs type="border-card" stretch>
            <el-tab-pane label="配置">
                <div class="title">{{getTitle}}</div>
                <component v-if="state.index !== undefined && state.index !== null"
                           :is="loader[config.components[config.sequence[state.index]].config.id].default"
                           :component="config.components[config.sequence[state.index]]"></component>
            </el-tab-pane>
            <el-tab-pane label="数据">
                <div class="title">{{getTitle}}</div>
                <Data v-if="config.components[config.sequence[state.index]]"
                      :component="config.components[config.sequence[state.index]]"
                      :state="state"></Data>
            </el-tab-pane>
            <el-tab-pane label="事件">
                <div class="title">{{getTitle}}</div>
                <Interaction v-if="config.components[config.sequence[state.index]]"
                             :component="config.components[config.sequence[state.index]]"></Interaction>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
    import Data from "./Data"
    import Interaction from "./Interaction"

    export default {
        name: "Base",
        props: ["config", "state"],
        data() {
            return {
                // 动态加载组件的存储
                loader: {},
            }
        },
        created() {
            this.loadConfig()
        },
        computed: {
            getTitle() {
                let id = this.config.sequence[this.state.index]
                return this.config.components[id] ? this.config.components[id].text : ""
            }
        },
        methods: {
            /**
             * 动态加载组件
             */
            loadConfig() {
                if (!this.config.sequence || !this.config.sequence.length) {
                    return
                }
                for (let i = 0; i < this.config.sequence.length; i++) {
                    let id = this.config.sequence[i]
                    let comp = this.config.components[id]
                    if (!comp || !comp.name || this.loader[id]) {
                        continue
                    }
                    let path = "components/screen/config/" + comp.category + "/" + comp.name
                    this.loader[id] = (require("../../../" + path + ".vue"))
                }
            },
        },
        watch: {
            "config.components": {
                immediate: true,
                handler: function (val, oldVal) {
                    this.loadConfig()
                }
            }
        },
        components: {
            "Data": Data,
            "Interaction": Interaction
        }
    }
</script>

